
.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

input {
    padding: 10px;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 10px;
}
.file-input-container {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
}
.file-input-container input[type="file"] {
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
}
.file-input-container input[type="file"]:hover {
    background-color: #fff;
    color: #000;
    border: 2px solid #000;
}
.file-input-label {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: 16px;
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    width: 100%;
    box-sizing: border-box;
}
.file-input-label:hover {
    background-color: #fff;
    color: #000;
    border: 2px solid #000;
}
.Back {
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: #000;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s, color 0.3s;
}
